.dashtl1_row1 {
  padding: 0 50px;
}
.dashtl1_card_row {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.dashtl_card {
  margin: 20px 0;
  box-shadow: 10px 15px 80px #dedede65;
  border-radius: 15px;
  width: 48%;
  padding: 20px;
}
.dashtl_card_row1 {
  display: flex;
  justify-content: space-between;
}
.dashtl1_card_col1 {
  display: flex;
}
img.prof_img {
  width: 100px;
  height: 100px;
  margin: 15px 3px;
  border-radius: 50%;
}
p.dashtl_p6 {
  margin: 0 10px;
  font-size: 29px;
}
.dashtl1_card_col2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
p.dashtl_card_p1 {
  font-size: 12px;
  margin: 0 5px;
  font-weight: 500;
  color: #9f9f9f;
}
p.dashtl_card_p2 {
  margin: 0;
  font-weight: 500;
  color: #5524fb;
}
.dashtl_card_row3 {
  display: flex;
  justify-content: center;
  font-size: 12px;
  margin: 0;
}

img.dot3_svg {
  height: 25px;
  width: 6px;
  margin-left: 30px;
}
.dashtl_card_row2 {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}
.dashtl_smallcard {
  width: 30%;
  background: #f9f9f9;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
}
p.dashtl_p4 {
  color: #5524fb;
  font-weight: 600;
  font-size: 26px;
  margin: 0;
}
p.dashtl_p5 {
  margin: 0;
  color: #121212;
  font-weight: 500;
  font-size: 14px;
}
.dashtl_card_col3 {
  cursor: pointer;
  height: 50px;
}
.dashtl_dropdown {
  position: absolute;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 10px 10px 30px #9d9d9d41;
  border-radius: 5px;
  margin-left: -128px;
  margin-top: -8px;
  width: 162px;
}
.dashtl_drop1 {
  font-size: 12px;
  margin: 5px;
  padding: 5px 15px;
  display: flex;
  justify-content: space-between;
}
p.drop_p1 {
  margin: 0;
  margin-right: 10px;
}
.dashtl_header {
  background: #121212;
  display: flex;
  padding: 40px;
}
.dashtl_h_col1 {
  width: 3%;
}
.dashtl_h_col2 {
  text-align: center;
  width: 20%;
}
p.dashtl_h_p1 {
  color: #fff;
  font-size: 20px;
  margin: 0;
  margin-bottom: 5px;
}
p.dashtl_h_p2 {
  color: #acacac;
  font-size: 12px;
  margin: 0px;
  margin-bottom: 17px;
}
.dashtl_h_col3 {
  display: flex;
  justify-content: space-between;
  width: 77%;
}
.dashtl_h_cards {
  border-radius: 10px;
  background: #946cff;
  width: 22%;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
}
p.dashtl_h_p3 {
  color: #fff;
  font-size: 18px;
  margin: 0;
  font-weight: 300;
}
p.dashtl_h_p4 {
  color: #fff;
  font-size: 35px;
  margin: 5px;
  font-weight: 600;
}
.dashtl_drop1:hover {
  background-color: #ece7fd;
  color: #121212;
  border-radius: 5px;
}
img.dash_active {
  margin: 5px;
}
