  border-radius: 15px;
  width: 32%;
  margin: 10px;
  margin-top: 30px;
  padding: 20px;
}

.dash_asm_row2 {
  display: flex;
  margin: 2px 20px;
}

p.dash_asm_p1 {
  font-size: 12px;
  font-weight: 600;
  margin: -2px;
  margin-left: 6px;
}
p.dash_asm_p2 {
  margin: 0;
  font-size: 22px;
}
p.dash_asm_p3 {
  font-size: 13px;
  margin: 0;
  font-weight: 500;
  color: #fff;
}
p.dash_asm_p4 {
  font-size: 24px;
  font-weight: 700;
  color: #fff;
  margin: 0;
}
.dash_asm_box1 {
  width: 135px;
  margin: 10px 7px;
  background-size: cover;
  padding: 10px;
  border-radius: 5px;
}
